import React, { Component } from 'react';
import Header from './Compoments/Header';
import {HashRouter as Router,Route} from 'react-router-dom';
import Home from './Container/Home';
import Start from './Container/Start';
import Api from './Container/Api';
import About from './Container/About';
import Detail from './Container/Detail';
import Login from './Container/Login';
export default class App extends Component {
  render() {
    return (
        <Router basename='/cnode/build'>
            <div>
                <Header/>
            </div>
            <div className='content'>
                <div className='panel'>
                    <Route path='/home/' component={Home}/>
                    <Route path='/start' component={Start}/>
                    <Route path='/api' component={Api}/>
                    <Route path='/about' component={About}/>
                    <Route path='/login' component={Login}/>
                    <Route path='/topic/:id' component={Detail}/>
                    
                </div>
                <div class='right'>
                    <div>
                        <div class='rightp'>
                            <p >CNode：Node.js专业中文社区</p>
                            <p>您可以登录或注册也可以</p>
                        </div>
                        
                        <div class='git'>通过Github登陆</div>
                    </div>
                    <div class='tt'></div>
                    <img class='rightimg' src="//static.cnodejs.org/FufeQ8S-sz6aKH5bvPXzVXvQG2Z-"/>
                    <img class='rightimg' src="//static.cnodejs.org/Fn4D6BhOTz1IswvmzeZ1q7QW1ls_"/>
                    <img class='rightimg' src="//static.cnodejs.org/FlajCCXkxZaOsuWp3k0iaiqfrJaS"/>
                    <div class='tt'></div>
                    <div class='rightp'>
                        <span>无人回复的话题</span>
                    </div>
                    
                </div>
                
            </div>
        </Router>
    );
  }
}
