import React, { Component } from 'react';
import {BrowserRouter as Router,Route,Link,Redirect,Switch,NavLink} from 'react-router-dom';
import {useState,useEffect} from 'react';
import All from './Home/All'
import Ask from './Home/Ask'
import Good from './Home/Good'
import Share from './Home/Share'
import Job from './Home/Job'


export default class Home extends Component {
  constructor(){
    super();
    this.state = {
        page:1
    }
  }
  render() {
    console.log(this.props);
    let {url}=this.props.match
    return (
        
        <div >
            <div class='header'>
                <Link to={`${url}/all`} class=' topic-tab'>全部</Link>
                <Link to={`${url}/good`} class='topic-tab '>精华</Link>
                <Link to={`${url}/share`} class='topic-tab'>分享</Link>
                <Link to={`${url}/ask`} class='topic-tab'>问答</Link>
                <Link to={`${url}/job`} class='topic-tab'>招聘</Link>
            </div>
            <div>
              <Switch>
                <Route path={`${url}/all`} component={All} />
                <Route path={`${url}/good`} component={Good} />
                <Route path={`${url}/share`} component={Share} />
                <Route path={`${url}/ask`} component={Ask} />
                <Route path={`${url}/job`} component={Job} />

                {/* <Redirect from={`${url}/all`} to={{pathname:`${url}/all/1`}}></Redirect>
                <Redirect from={`${url}/good`} to={{pathname:`${url}/good/1`}}></Redirect>
                <Redirect from={`${url}/share`} to={{pathname:`${url}/share/1`}}></Redirect>
                <Redirect from={`${url}/ask`} to={{pathname:`${url}/ask/1`}}></Redirect>
                <Redirect from={`${url}/job`} to={{pathname:`${url}/job/1`}}></Redirect> */}
              </Switch>
            </div>
            {/* <div class='pagination'>
              <ul>
                <li class='lie'><Link to={`${url}/1`} class='topic-tab '>1</Link></li>
                <li><Link to={`${url}/2`} class='topic-tab '>2</Link></li>
                <li><Link to={`${url}/3`} class='topic-tab'>3</Link></li>
                <li><Link to={`${url}/4`} class='topic-tab'>4</Link></li>
                <li><Link to={`${url}/5`} class='topic-tab'>5</Link></li>
                <li><Link to={`${url}/6`} class='topic-tab'>6</Link></li>
                <li><Link to={`${url}/7`} class='topic-tab'>7</Link></li>
                <li><Link to={`${url}/8`} class='topic-tab'>8</Link></li>
                <li><Link to={`${url}/9`} class='topic-tab'>9</Link></li>
                <li><Link to={`${url}/10`} class='topic-tab'>10</Link></li>
              </ul>
            </div> */}
        </div>
    );
  }
}
