import React, { Component } from 'react';
import {BrowserRouter as Router,Route,Link} from 'react-router-dom';
export default class Login extends Component {
  render() {
    return (
            <div class='box'>
            
                <form>
                    <div>
                        <div class='one'>
                            <label>用户名</label>
                            <input  class='name' id='name' name='name' size='30' type='text'/>
                        </div>
                        <div class='two'>
                            <label>密码</label>
                            <input  class='pass' id='pass' name='pass' size='30' type='password'/>
                        </div>
                    </div>
                    <div>
                        <Link to='/home/all/1'><input id='submit' type='submit' class='span-primary' value='登录'/></Link>
                    </div>
                </form>
            </div>
     
    );
  }
}
